.contact {
  padding-top: 72px;
  @include onTablet {
    padding-top: 180px;
  }

  background-color: $dark-background-color;
  border-radius: 30px 30px 0 0;

  &__send-us {
    margin-bottom: 80px;
    @include onTablet {
      margin-bottom: 120px;
    }
  }

  &__title {
    margin-bottom: 48px;
    line-height: 130%;
    text-align: left;

    color: #fff !important;
  }

  &__wrap-link {
    &:not(:last-child) {
      margin-bottom: 48px;
    }
  }

  &__subtitle {
    margin-bottom: 8px;

    font-weight: 700;
    font-size: 13px;
    line-height: 20px;

    letter-spacing: 3px;
    text-transform: uppercase;
    opacity: 0.5;
  }

  &__link {
    @extend %OpenSans600;
    font-size: 24px;
    line-height: 150%;
    transition: all $transition-duration;

    &:hover {
      transition: all $transition-duration;
      color: $blue-color;
    }
  }

  &__wrap-socials {
    margin-top: 20px;

    display: flex;
    column-gap: 32px;
  }

  &__social {
    @include square(32px);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }

  &__social-fb {
    background-image: url(../images/contact/facebook.svg);
    transition: all $transition-duration;

    &:hover {
      background-image: url(../images/contact/facebook-active.svg);
      transform: scale(1.4);
      transition: all $transition-duration;
    }
  }

  &__social-tw {
    background-image: url(../images/contact/twitter.svg);
    transition: all $transition-duration;

    &:hover {
      background-image: url(../images/contact/twitter-active.svg);
      transform: scale(1.4);
      transition: all $transition-duration;
    }
  }

  &__social-inst {
    background-image: url(../images/contact/instagram.svg);
    transition: all $transition-duration;

    &:hover {
      background-image: url(../images/contact/instagram-active.svg);
      transform: scale(1.4);
      transition: all $transition-duration;
    }
  }
}
