.service {
  padding: 32px;

  background-color: $white-color;
  box-shadow: 0 9px 18px rgba(37, 41, 49, 0.03);
  border-radius: 16px;

  &:hover {
    transform: scale (1.05);
  }

  &__number {
    margin-bottom: 24px;
    width: 46px;
    padding: 2px 0;

    font-size: 13px;
    line-height: 20px;
    text-align: center;
    color: $blue-color;

    background-color: #e9effe;
    border-radius: 23px;
  }

  &__list {
    margin-bottom: 32px;
  }

  &__item {
    @extend %OpenSans600;
    font-size: 13px;
    line-height: 150%;
    color: $text-color;
  }

  &__action {
    display: flex;
    align-items: center;
    width: max-content;

    font-size: 15px;
    line-height: 22px;
    color: #334563;

    transition: all $transition-duration;
    &:hover {
      font-size: 18px;
      transition: all $transition-duration;
    }
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;

    margin-right: 16px;
    width: 40px;
    height: 40px;

    border-radius: 50%;
    background: $blue-color;
  }

  &__button-arrow {
    @include square(16px);
    background-image: url(../images/sevise/Vector.svg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }
}
