@mixin onTablet {
  @media (min-width: 640px) {
    @content;
  }
}

@mixin onSmallDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: 1280px) {
    @content;
  }
}

@mixin onLargeScreen {
  @media (min-width: 2560px) {
    @content;
  }
}

@mixin square($size) {
  width: $size;
  height: $size;
}
